function getTimeInfo() {
  const begin = new Date(2009, 0, 4, 2, 15, 5);
  const now = new Date();
  const dayOffset = (now - begin) / (1000 * 3600 * 24);
  const year = Math.floor(dayOffset / 365);
  const month = Math.floor((dayOffset % 365) / 30);
  const day = Math.floor((dayOffset % 365) % 30);
  $('.year').html(year);
  $('.month').html(month);
  $('.day').html(day);
}

